.Badge__section-name-list {
    background: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
    margin-bottom: 1rem;
    border-radius: 8px 8px 8px 8px;
    display: flex;
    padding: 1rem 0;
    width: 100%;
}

.Badge__avatar-list {
    border-radius: 50%;
    margin-right: 1rem;
    width: 70px;
    height: 70px;
    display: flex;
    margin-left: 1rem;
}

.Badge__link {
    display: inherit;
}

.twitter__blue_font {
    color: #38a1f3;
}

.tw__logo {
    width: 25px;
    height: 25px;
}